@font-face {
    font-family: 'SevenSegment';
    src: local('SevenSegment'), url(../../fonts/SevenSegment.ttf) format('truetype')
  }

.Screen-Div {
    text-align: center; 
    display: inline-block;
    background-color: #395f37;
    width: 300px;
    height: 40px;
    font-family: SevenSegment;
    font-size: 35px;
}

.Display {
  display: flex;
  width: 300px;
  height: 40px
}

.Result {
  width: 220px;
  height: 40px;
}

.Equals-Button, .Clear-Button {
  width: 40px;
  height: 40px;
}

@media screen and (max-width: 600px) {
  .Screen-Div {
    text-align: center; 
    display: inline-block;
    background-color: #395f37;
    width: 250px;
    height: 30px;
    font-family: SevenSegment;
    font-size: 35px;
  }
  .Display {
    display: flex;
    width: 250px;
    height: 30px
  }

  .Result {
    width: 190px;
    height: 30px;
  }

  .Equals-Button, .Clear-Button {
    width: 30px;
    height: 30px;
  }
}